import { createBrowserHistory } from 'history';
import './App.css';
import React from 'react';
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link
} from 'react-router-dom';

function App() {
  const history = createBrowserHistory();
  return (
    <Router history={history}>
      <Switch>
        <Route exact path="/">
          <Home />
        </Route>
        <Route path="/about">
          <About />
        </Route>
        <Route path="/dashboard">
          <Dashboard />
        </Route>
      </Switch>
      <ul class="navtabs">
        <li>
          <Link to="/">Home</Link>
        </li>
        <li>
          <Link to="/about">About</Link>
        </li>
        <li>
          <Link to="/dashboard">Dashboard</Link>
        </li>
      </ul>
    </Router>
  );
}

function Home() {
  return (
    <>
      <h2 class="stickyheader">Body Scrolling</h2>
      <div class="container">
        <div class="box darkgray"/>
        <div class="box darkgray"/>
        <div class="box darkgray"/>
        <div class="box darkgray"/>
        <div class="box darkgray"/>
        <div class="box darkgray"/>
      </div>
    </>
  );
}

function About() {
  return (
    <>
      <h2 class="stickyheader">Fixed container height 100vh</h2>
      <div class="container fixedHeightVh">
        <div class="box gray"/>
        <div class="box gray"/>
        <div class="box gray"/>
        <div class="box gray"/>
        <div class="box gray"/>
        <div class="box gray"/>
      </div>
    </>
  );
}

function Dashboard() {
  return (
    <>
      <h2 class="stickyheader">Fixed container height 100%</h2>
      <div class="container fixedHeightPercent">
        <div class="box lightgray"/>
        <div class="box lightgray"/>
        <div class="box lightgray"/>
        <div class="box lightgray"/>
        <div class="box lightgray"/>
        <div class="box lightgray"/>
      </div>
    </>
  );
}

export default App;
